const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:3000/api'

export async function createCheckoutSession(planId, userId, userEmail) {
  try {
    const response = await fetch(`${API_URL}/create-checkout-session`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        plan: planId,
        userId,
        userEmail
      }),
    })

    if (!response.ok) {
      throw new Error('Failed to create checkout session')
    }

    return response.json()
  } catch (error) {
    console.error('Error creating checkout session:', error)
    throw error
  }
}

export async function getSubscriptionStatus(userId) {
  try {
    // For free plan, return default values without API call
    return {
      tier: 'free',
      projectLimit: 3,
      status: 'active',
      validUntil: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000)
    }
  } catch (error) {
    console.error('Error getting subscription status:', error)
    throw error
  }
}

export async function updateSubscription(userId, plan) {
  try {
    if (plan === 'free') {
      return {
        tier: 'free',
        projectLimit: 3,
        status: 'active',
        validUntil: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000)
      }
    }

    const response = await fetch(`${API_URL}/update-subscription`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ userId, plan }),
    })

    if (!response.ok) {
      throw new Error('Failed to update subscription')
    }

    return response.json()
  } catch (error) {
    console.error('Error updating subscription:', error)
    throw error
  }
} 